body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
}

.App-actions {
  position: absolute;
  right: 15px;
  top: 0;
}

.App-actions-hover {
  width: 50px;
  height: 20px;
}

.App-actions-overlay {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.App-actions-button {
  margin-bottom: 10px;
}

iframe {
  border-style: none;
  height: 100%;
  margin:0;
}
